import { vm }  from '@/main'

import { storage } from '@/utils/storage'

const locationData = {
  city: 'San Antonio',
  phone: '844-230-6576',
  address: '101 Bowie St, 78205'
}

const socialNetworks = [
  { file: '/imgs/social/insta.png', name: 'Instagram', link: 'https://www.instagram.com/movelikewedo/' },
  { file: '/imgs/social/youtubes.png', name: 'Youtube', link: 'https://www.youtube.com/c/movelikewedo' },
  { file: '/imgs/social/facebook.png', name: 'Facebook', link: 'https://business.facebook.com/movelikewedo' },
  { file: '/imgs/social/twitter.png', name: 'Twitter', link: 'https://twitter.com/movelikewedo' }
]

const siteNavigation = [
  {img: 'menu_1', name: 'Hotel Information', index: 1, description: '<p>Information about the Marriott River Center</p>', path: '/amenities'},
  {img: 'menu_2', name: 'Tourism', index: 2, description: '<p>Sites and landmarks in the San Antonio area.</p>', path: '/tourism'},
  {img: 'menu_3', name: 'What\'s Happening', index: 3, description: '<p>Check out upcoming events brought to you by EventBrite.</p>', path: '/events'},
  {img: 'menu_4', name: 'Where to Eat', index: 4, description: '<p>Local favourites for eating out.</p>', path: '/dining'}
]

const footerNavigation = [
  {
    id: 0,
    options: {
      name: 'main'
    }
  },
  {
    id: 1,
    options: {
      name: 'amenities'
    }
  },
  {
    id: 2,
    options: {
      name: 'tourism'
    }
  },
  {
    id: 3,
    options: {
      name: 'events'
    }
  },
  {
    id: 4,
    options: {
      name: 'eat'
    }
  },
]

const state = {
  ebstate: 'sanantonio',
  currentTab: 0,
  subnav: 0,
  socialData: socialNetworks,
  siteNavigation,
  footerNavigation,
  footerData: null,
  locationData,
  currentLocation: null
}

const mutations = {
  setLocation (state, loc) {
    state.currentLocation = loc
  },
  setFooterNav (state, obj) {
    state.footerData = obj
  },
  setEbState (state, obj) {
    state.ebstate = obj
  }
}

const getters = {
  socialNetworks (state) {
    return state.socialData
  },
  currentLocation (state) {
    return state.currentLocation
  },
  siteNavigation (state) {
    return state.siteNavigation
  },
  businessLocation (state) {
    return state.location
  },
  locationData (state) {
    return state.locationData
  },
  footerData (state) {
    return state.footerData
  },
  ebstate (state) {
    return state.ebstate
  }
}

const actions = {
  setAppLocation ({ commit, state }, obj) {
    storage.storeValue('location', obj)
    for(let i = 0; i < footerNavigation.length; i++){
      let t = footerNavigation[i]
      if(t.id === obj.index){
        commit('setFooterNav', t)
      }
    }
    vm.$router.push(obj.path)
  },
  setEbState({commit, state}, obj) {
    storage.storeValue('ebstate', obj)
    commit('setEbState', obj)
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}



