<template>
  <div>
    <div v-for="item in siteNavigation" v-bind:key="item._id">
      <menu-tile v-bind:item="item"></menu-tile>
    </div>
  </div>
</template>

<script>
import MainMenu from '@/components/uiComponents/MainMenu'
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'main-menu',
  components: {
    'menu-tile': MainMenu
  },
  data () {
    return {
    }
  },
  
  mounted () {
    this.setAppLocation({path: '/', index: 0})
  }, 
  methods: {
    ...mapActions([
      'setAppLocation'
    ])
  },
  computed: {
    ...mapGetters([
      'siteNavigation'
    ])
  }
}
</script>

<style>

</style>



